import AddBlock   from './add_block'
import BlockTypes from 'stores/block_type_store'
import React      from 'react'

let BlockMenu = React.createClass({

  propTypes: {
    block : React.PropTypes.object,
    onAdd : React.PropTypes.func.isRequired
  },

  getDefaultProps() {
    return {
      position : 0
    }
  },

  getButton(blockType) {
    let { block, onAdd } = this.props

    return (<AddBlock key={ blockType.id }
                      block={ block }
                      blockType={ blockType }
                      onAdd={ onAdd } />)
  },

  render() {
    let types = this.props.flux.get(BlockTypes).filter(i => !i.private)

    return types.length ? (
      <nav className="col-menu" role="navigation">
        { types.map(this.getButton) }
      </nav>
    ) : null
  },

})

export default BlockMenu



