import Block        from 'components/block'
import BlockTypes   from 'stores/block_type_store'
import Blocks       from 'stores/block_store'
import BlockActions from 'actions/blocks'
import BlockMenu    from 'components/block_menu'
import React        from 'react'

let EditorBlock = React.createClass({

  propTypes: {
    block : React.PropTypes.object.isRequired,
    flux  : React.PropTypes.object.isRequired
  },

  getBlock(block): any {
    return React.cloneElement(this, { block })
  },

  render(): any {
    let { block,  flux } = this.props

    let children  = flux.get(Blocks).filter(i => i.parent == block)
    let blockType = flux.get(BlockTypes).filter(i => i.id === block.type)[0]

    return (
      <Block block={ block } blockType={ blockType } onUpdate={ this._onUpdate } onDestroy={ this._onDestroy }>
        { children.map(this.getBlock) }
        <BlockMenu key="block_menu" block={ block } flux={ flux } onAdd={ this._onAdd } />
      </Block>
    )
  },

  _onAdd(params) {
    this.props.flux.send(BlockActions.create, params)
  },

  _onUpdate(params) {
    this.props.flux.send(BlockActions.update, params)
  },

  _onDestroy(id) {
    this.props.flux.send(BlockActions.destroy, id)
  }
})

export default EditorBlock



